:root {
  --font-body: Arial, sans-serif;
}

:root {
  --facebook: hsl(221, 44%, 41%);
  --brand: hsl(88, 50%, 53%);
  --orange: hsl(12, 81%, 57%);
  --pinterest: hsl(358, 73%, 46%);
  --white: hsl(0, 0%, 100%);
  --gray-light: hsl(0, 0%, 96%);
  --gray-dark: hsl(0, 0%, 88%);
  --text: hsl(0, 0%, 46%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a,
p,
body {
  margin: 0;
  padding: 0;
  color: var(--text);
  font-family: "Lato", sans-serif;
  transition: all 0.2s ease-in-out;
}

input {
  background-color: transparent;
  padding: 0.625rem 0.9375rem;
}

.logo {
  display: inline-block;
}

.input {
  float: left;
  min-width: 100%;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--gray-dark);
}
.input--sm {
  min-width: 20.3125rem;
  border-radius: 0.3125rem 0 0 0.3125rem;
}

.button {
  cursor: pointer;
  color: var(--white);
  background-color: var(--brand);
  border: 0.0625rem solid var(--brand);
  transition: all 0.2s ease-in-out;
  padding: 0.625rem 1.875rem;
  border-radius: 0.3125rem;
}
.button:hover {
  border-color: #71a437;
  background-color: #71a437;
}
.button--transparent {
  color: var(--text);
  border-color: var(--text);
  background-color: transparent;
}
.button--transparent:hover {
  border-color: unset;
  background-color: unset;
}
.button--sm {
  padding: 0.625rem 0.9375rem;
}
.button--lg {
  font-weight: 600;
  font-size: 1rem;
  padding: 0.9375rem 3.125rem;
}

.title {
  font-weight: 400;
  font-size: 1.875rem;
  color: var(--brand);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a:hover,
.button--transparent:hover {
  color: var(--brand);
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  padding: 0 0.625rem;
  max-width: 81.25rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header__top, .header__bottom nav {
  display: flex;
  align-items: center;
  min-height: 2.8125rem;
}
.header__top {
  justify-content: space-between;
  min-height: 6.25rem;
}
.header__top button {
  border-radius: 0 0.3125rem 0.3125rem 0;
}
.header__bottom {
  background-color: var(--gray-light);
}
.header__bottom nav *:not(:last-child) {
  margin-right: 1.875rem;
}
.header__buttons {
  display: flex;
  align-items: center;
}
.header__buttons *:not(:last-child) {
  margin-right: 1.5625rem;
}

.popup {
  position: absolute;
  z-index: 2;
  min-width: 100%;
  min-height: 100vh;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
}
.popup:target {
  visibility: visible;
  opacity: 1;
}
.popup__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: 31.25rem;
  border-radius: 0.3125rem;
  padding: 3.75rem 3.125rem 1.5625rem;
  border: 0.0625rem solid var(--text);
  transform: translate(-50%, -50%);
  background-color: #fff;
}
.popup__inner *:not(:last-child, .division) {
  margin-bottom: 1.25rem;
}
.popup__inner .close {
  position: absolute;
  top: 0.9375rem;
  right: 0.9375rem;
}
.popup__inner .division {
  margin: 2.5rem 0;
  position: relative;
  max-height: 0.0625rem;
  background-color: rgba(117, 117, 117, 0.1);
}
.popup__inner .division::after {
  content: "or";
  padding: 0.3125rem;
  background-color: #fff;
  position: relative;
  top: -0.625rem;
}
.popup__inner .division:nth-child(6) {
  margin: 2.5rem -3.125rem;
}
.popup__inner .division:nth-child(6)::after {
  opacity: 0;
}
.popup__help {
  display: inline-flex;
  justify-content: space-between;
  min-width: 100%;
}
.popup__buttons {
  font-size: 0;
}
.popup__buttons *:not(:last-child) {
  margin-right: 1.125rem;
}
.popup__remember input {
  display: none;
}
.popup__remember label {
  position: relative;
  cursor: pointer;
}
.popup__remember label:before {
  content: "";
  padding: 0.5rem;
  margin-right: 0.3125rem;
  border-radius: 0.3125rem;
  display: inline-block;
  border: 0.0625rem solid var(--gray-dark);
  vertical-align: middle;
  position: relative;
}
.popup__remember input:checked + label:after {
  content: "";
  min-width: 0.375rem;
  min-height: 0.625rem;
  transform: rotate(45deg);
  border: 0.0625rem solid var(--brand);
  border-width: 0 0.125rem 0.125rem 0;
  position: absolute;
  top: 0.3125rem;
  left: 0.375rem;
}
.popup__social {
  font-size: 0;
}
.popup__social p {
  font-size: 0.875rem;
  color: var(--black);
}
.popup__social a {
  padding: 0.9375rem;
  border-radius: 50%;
  min-width: 3.125rem;
  min-height: 3.125rem;
  display: inline-block;
  margin-right: 1.875rem;
  background-color: var(--facebook);
}
.popup__social a:nth-child(3) {
  background-color: var(--orange);
}
.popup__social a:nth-child(3) img {
  max-width: 1.25rem;
  min-height: 1.25rem;
}
.popup__social a:nth-child(4) {
  background-color: var(--pinterest);
  margin-right: 0;
}
.popup__social a img {
  min-width: 0.9375rem;
  max-height: 1.09375rem;
  transform: scale(1.5);
}
.popup__login {
  font-size: 0.875rem;
}
.popup__login .button {
  color: var(--brand);
  margin-left: 1.25rem;
  border-color: var(--brand);
}

.hero {
  background: url("../images/background.jpg") no-repeat 50%/cover;
  text-align: center;
  position: relative;
  color: var(--white);
  flex-grow: 1;
}
.hero__inner {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.hero__title {
  line-height: 1.4;
  font-weight: 300;
  font-size: 3.125rem;
}

.seller {
  margin-top: 18.25rem;
  margin-bottom: 7.25rem;
}
.seller__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.seller__item {
  max-width: 23.75rem;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--gray-dark);
  margin-bottom: 2.5rem;
}
.seller__item > img {
  border-radius: 0.3125rem 0.3125rem 0 0;
  max-width: 100%;
}
.seller__text {
  padding: 1.25rem 1.25rem 0.75rem;
}
.seller__title {
  font-size: 0.875rem;
  color: var(--black);
  margin: 0.5rem 0;
}
.seller__title a {
  float: right;
}
.seller__title img {
  margin: 0;
}
.seller__suptitle {
  color: var(--brand);
}
.seller .explore {
  text-align: center;
}

.swiper {
  box-shadow: 0 0.75rem 4.25rem var(--text);
  padding: 1.875rem 4.375rem 2.8125rem;
  background-color: #fff;
  border-radius: 0.3125rem;
  position: absolute;
  max-width: 100%;
  top: -6.25rem;
}
.swiper-slide {
  text-align: center;
}
.swiper-slide img {
  max-width: 100%;
}
.swiper-button-prev, .swiper-button-next {
  max-width: 0.75rem;
  max-height: 1.25rem;
}
.swiper-button-prev::after, .swiper-button-next::after {
  content: "";
}
.swiper-button-prev {
  left: 1.875rem;
  background-image: url("../images/arrow-left.svg");
}
.swiper-button-next {
  right: 1.875rem;
  background-image: url("../images/arrow-right.svg");
}

.info__item {
  display: flex;
  align-items: end;
  justify-content: space-between;
  max-width: 72.1875rem;
  margin-bottom: 9.25rem;
}
.info__item:last-child {
  margin-bottom: 7.5rem;
  align-items: center;
  margin-left: auto;
}
.info__text {
  max-width: 37.5rem;
}
.info__text p {
  margin-bottom: 1.125rem;
}